.visible {
  visibility: visible;
  color: white;
  background: black;
  width: max-content;
  display: block;
  /* padding: 0.7rem 1.7rem; */
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

.hidden {
  visibility: hidden;
  color: white;
  background: black;
  width: max-content;
  display: block;
  /* padding: 0.7rem 1.7rem; */
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

